.home--carousel {
  .slick-list {
    height: 100%;

    .slick-slide {
      &.slick-active .bg_carousel--describe {
        -webkit-animation: slide-right 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) 150ms;
        animation: slide-right 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) 150ms;
      }

      .bg_carousel--subImage img {
        opacity: 0;
      }
      &.slick-active .bg_carousel--subImage img {
        -webkit-animation: flip-vertical-right 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 150ms;
        animation: flip-vertical-right 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 150ms;
        opacity: 1;
      }
    }
  }

  .bg_carousel {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;

    .bg_carousel--overview {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 4;
    }

    button.bg_carousel--btnDetail {
      background-color: #fc3030;
      -webkit-box-shadow: 0px 0px 15px 8px rgb(248 3 3);
      -moz-box-shadow: 0px 0px 15px 8px rgb(248 3 3);
      box-shadow: 0px 0px 15px 8px rgb(248 3 3);

      &:hover {
        transform: translateY(-2px);
        -webkit-box-shadow: 0px 0px 21px 9px rgb(248 3 3);
        -moz-box-shadow: 0px 0px 21px 9px rgb(248 3 3);
        box-shadow: 0px 0px 21px 9px rgb(248 3 3);
      }
    }

    button.bg_carousel--btnTrailer:hover {
      -webkit-box-shadow: 0px 0px 15px 8px #fff;
      -moz-box-shadow: 0px 0px 15px 8px #fff;
      box-shadow: 0px 0px 15px 8px #fff;
      transform: translateY(-2px);
    }
  }
}

.swal2-html-container {
  margin: 0 !important;
}

@media (max-width: 480px) {
  .bg_carousel--title,
  .bg_carousel--overview {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }

  .bg_carousel--title {
    min-height: 64px;
    -webkit-line-clamp: 2;
  }

  .home--carousel .bg_carousel .bg_carousel--overview {
    -webkit-line-clamp: 3;
  }
}
